* {
  box-sizing: border-box;
}
#quote-box {
  border-radius: 5px;
  border: 1px solid lightblue;
  box-shadow: 3px 3px 10px lightblue;
  width: 50vw;
  height: 300px;
  position: fixed;
  top: 25vh;
  left: 25vw;
  padding: 10px;
}

#text {
  display: block;
  color: hsl(234, 89%, 64%);
  text-align: center;
  padding-top: 10px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 28px;
  margin-bottom: 10px;
}

#author {
  display: block;
  text-align: right;
}

#new-quote {
  display: block;
  width: 90%;
  height: 30px;
  position: absolute;
  bottom: 40px;
  left: 5%;
  background-color: transparent;
  border: 1px solid lightblue;
  border-radius: 5px;
  box-shadow: 2px 2px 3px lightblue;
  font-size: 16px;
}

#tweet-quote {
  display: block;
  position: absolute;
  bottom: 10px;
  width: 100%;
  text-align: center;
  text-decoration: none;
  color: rgb(82, 79, 236);
}

#new-quote:hover {
  background-color: lightblue;
  color: white;
  box-shadow: 3px 3px 5px black;
  transform: scale(1.05);
}
#tweet-quote:hover {
  color: blue;
}
